import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { InputGroupInline, InputType, SIMPLE, WindowComponent, WindowComponentProps } from 'summer';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { TRABALHADOR_QUALIFICACAO_CADASTRAL_ENUM } from '../../../../../models/enumerated/mfp/trabalhadorQualificacaoCadastralEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, QualificacaoCadastralRelRV, REPORT_EVENT, REPORT_TITLE } from './qualificacaoCadastralRelRV';

const DEFAULT_VALUES: QualificacaoCadastralRelRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, SIMPLE.value),
  exibirConfiguracaoDadosEmpresa: true,
};

const QualificacaoCadastralReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<QualificacaoCadastralRelRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  return (
    <WindowComponent
      title="Dados da Qualificação Cadastral - eSocial"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[]}
    >
      <InputGroupInline
        id="exibir"
        label="Exibir"
        isPrependLabel={false}
        placeholder="Selecione o tipo de Qualificação Cadastral"
        formProps={{ ...formProps, validation: { required: true } }}
        inputDropDownProps={{
          optionsProvider: TRABALHADOR_QUALIFICACAO_CADASTRAL_ENUM,
        }}
        type={InputType.DROP_DOWN}
      />

      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      />
    </WindowComponent>
  );
};

export default QualificacaoCadastralReportView;
